import { Component, OnInit, ViewChild, ElementRef } from '@angular/core';
import {FillDataStepsChangerService} from '../services/fill-data-steps-changer.service';
import {ScrollService} from '../services/scroll.service';
import {TranslateService} from "ng2-translate";
import {Router, NavigationEnd} from '@angular/router';
import {SharedModelService} from '../services/shared-model.service';
import { CustomPolyfills } from '../../custom-polyfills'
import { Http } from '@angular/http';
import { environment } from '../../environments/environment'

@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit {
  @ViewChild('formContainer') formContainer: ElementRef;
  formContainerHeight = '100vh';
  itemsNames = ['hsbc-main.vos_informations','hsbc-main.votre_situation','hsbc-main.votre_compte','hsbc-main.validation'];
  items = [];
  activeIndex ={step:0,subStep:0};
  subSteps=[4,4,4,1];
  environment = environment;
  
  constructor(
    private fillDataStepChanger:FillDataStepsChangerService,
    private translateService: TranslateService, 
    private router: Router, 
    private _scroll: ScrollService,
    private _sharedModelService: SharedModelService,
    private _http: Http) { 
    
    this.fillDataStepChanger.getCurrentStep().subscribe((val)=>{
      this.activeIndex = val;
    })

    translateService.onLangChange.subscribe((ev)=>{
      let customEvent;
      if(ev.lang == 'fr'){
        customEvent = CustomPolyfills.createNewEvent('lang-change-fr-ng')
      }
      else {
        customEvent = CustomPolyfills.createNewEvent('lang-change-en-ng')
      }
      window.dispatchEvent( customEvent );
      for(let i = 0;i< this.itemsNames.length;i++){
        translateService.get(this.itemsNames[i],{value : 'VOS INFORMATIONS'}).subscribe((val)=>{
        this.items[i] = {label:val,subSteps:this.subSteps[i]};
      })
      }
    })

  }

  ngOnInit() {
    let trackerNode;
     
    setInterval(() => {
      this.refreshSession()
    }, 300000);
    
    this.router.events.subscribe(event => {
      if (event instanceof NavigationEnd) {
        this._scroll.scrollTo(0, 500)

        if(trackerNode){
          try {
            document.body.removeChild(trackerNode);  
          }
          catch (e){
            // console.log('=> Error removing node trackers data', e);
          }
        }

        let cdata = {};
        this.getTrackerValue(event.urlAfterRedirects, 'ti', (value) => {
          let ti = value;
          this.getTrackerValue(event.urlAfterRedirects, 'dcsuri', (value) => {
            let dcsuri = value;
            try {
              trackerNode = document.createElement('script');
              trackerNode.text = '/*<![CDATA[*/HSBC.PAGE.ti="'+ti+'";HSBC.LOG.dcsuri="'+dcsuri+'";/*]]>*/';
              document.body.appendChild(trackerNode)
              window['utag'].view();
            }
            catch (e){
              console.log('=> Error injecting trackers data', e);
            }
          });
        });
      }
    });

  }


  getTrackerValue(trackerUrl, type , callback){
    let key;
    if(this._sharedModelService.model && this._sharedModelService.model.accountType && this._sharedModelService.model.accountType === 'jointAccount'){
     key = 'hsbc-trackers-joint.' + trackerUrl.split('/')[1].replace(/-/g, '_')
     if(trackerUrl.split('/')[2]){
       key  += '_' + trackerUrl.split('/')[2] 
     }
     key +=  '_' + type;

     console.log('=> KEY', key)
    }
    else {
      key = 'hsbc-trackers-solo.' + trackerUrl.split('/')[1].replace(/-/g, '_') + '_' + type
    }
    this.translateService.get(key).subscribe((value) => {
      callback(value);
    })  
  }


  refreshSession(){
    this._http.post(environment.pingPath, {})
    .map(res => res.json())
    .subscribe((resp) => {
       
    });
  }
}



