import {Component, forwardRef, Input, Output, EventEmitter, ElementRef, NgModule, SimpleChanges} from '@angular/core';
import {NG_VALUE_ACCESSOR, ControlValueAccessor} from "@angular/forms";
import {CommonModule} from "@angular/common";
import { TranslateService } from 'ng2-translate';

export interface SelectItem {
  label: string;
  icon : string;
  value: any;
}
export const SELECTBUTTON_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => SegmentControlComponent),
  multi: true
};

@Component({
  selector: 'pib-segment-control',
  template: `
               <div [ngClass]="{'ui-state-focus-segement': focused}" 
                    [ngStyle]="style" [class]="'ui-buttonset ui-widget ui-corner-all ui-buttonset-'+ options.length + ' '+styleClass">
                    <div class="ui-helper-hidden-accessible">
                        <input #in type="text" readonly (focus)="onInputFocus($event)" (blur)="onInputBlur($event)" (keydown)="onKeydown($event)" [disabled]="disabled" aria-hidden="true" [attr.tabindex]="tabindex" [attr.aria-labelledby]="labelledby"  [attr.aria-label]="ariaLabel" >
                    </div>
                  <div *ngFor="let option of options;" 
                       [class]="itemClass"
                        [attr.aria-labelledby]="uniqueId"
                        [attr.aria-invalid]="hasErrors()"
                        style="background-color: white"
                        [ngClass]="{'ui-button ui-widget ui-state-default ui-button-text-only':true,
                                    'ui-state-active':isSelected(option), 
                                    'ui-state-disabled':disabled,
                                    'icon-exist': option.icon,
                                    'form__input--error':messageType == 'ERROR' && hasErrors() && (stepSubmitted),
                                    'form__input--warning':messageType == 'WARNING' && hasErrors() && (stepSubmitted)
                                  }" 
                            (click)="onItemClick($event,option)">
                      <div *ngIf="option.icon">  <i class="icon {{option.icon}}"></i> </div>
                      <span [class]="itemTextClass" [ngClass]="{'ui-button-text ui-c':true}">{{option.label}}</span>
                  </div>
               </div>
               <div [id]="uniqueId" class="form__message" [ngClass] = "{'form__message--error':messageType == 'ERROR',
                                                'form__message--warning':messageType == 'WARNING'}"
                                    *ngIf="hasErrors() && showMessage && (stepSubmitted)">
                                <i class="icon" [ngClass] = "{'icon-circle-delete' :messageType == 'ERROR',
                                          'icon-circle-error': messageType == 'WARNING'}"></i>
                                    {{messageTrad}} {{inputLabel}}
              </div>
              `,
  styles:[`
    .ui-state-focus-segement{
      border: 1px #253038 solid !important;
    }
  `],
  providers: [SELECTBUTTON_VALUE_ACCESSOR]
})
export class SegmentControlComponent implements ControlValueAccessor {

  @Input() labelledby: any;
  @Input() ariaLabel: string;

  @Input() options: SelectItem[];

  @Input() multiple: boolean;

  @Input() style: any;

  @Input() styleClass: string;

  @Input() itemClass: string;

  @Input() disabled: boolean;

  @Input() itemTextClass: string;

  @Input() public message: any;

  @Input() public type : string = 'text';

  @Input() public messageType: string = 'ERROR';

  @Input() public stepSubmitted: boolean;

  @Output() onChange: EventEmitter<any> = new EventEmitter();
  @Input() public showMessage: boolean = true;
  value: any;
  public focused = false;
  private messageTrad: string;
  private inputLabel: string;
  private uniqueId : string;

  constructor(private _translate: TranslateService, private inputContainer: ElementRef) { 
    this._translate.onLangChange.subscribe(() => {
      if(this.message){
        this.updateTrad(this.message);
      }
    })
    this.uniqueId = 'error_' + Date.now().toString();
  }

  ngOnChanges(changes: SimpleChanges){
    if(changes['message'] && changes['message'].currentValue){
      this.updateTrad(changes['message'].currentValue);
    }
  }

  updateTrad(key){
    this._translate.get(key).subscribe((value) => {
      this.messageTrad = value;
    })
    if(key == 'hsbc-main.error_field_select_required_label'){
      try {
        let i = 0;
        let parentNode = this.inputContainer.nativeElement.parentNode;
        let check = false;
        while(i < 10){
          if(parentNode.className == 'form__group'){
            check = true;
            console.log(parentNode);
            break;
          }
          else{
            parentNode = parentNode.parentNode;
          }
          i++;
        }

        if(check){
          let label = parentNode.querySelector('.form__label').cloneNode(true);
          if(label.querySelector('help-button')){
            label.removeChild(label.querySelector('help-button'));  
          }
          if(label.querySelector('span')){
            label.removeChild(label.querySelector('span'));  
          }
          label = label.innerHTML;
          this.inputLabel = label.toLowerCase();  
        }
      }
      catch (e){
        this._translate.get('hsbc-main.error_field_required').subscribe((value) => {
          this.messageTrad = value;
        })
      }
    }
  }

  onModelChange: Function = () => {};

  onModelTouched: Function = () => {};

  writeValue(value: any) : void {
    this.value = value;
  }

  registerOnChange(fn: Function): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: Function): void {
    this.onModelTouched = fn;
  }

  setDisabledState(val: boolean): void {
    this.disabled = val;
  }

  onItemClick(event, option: SelectItem) {
    if(this.disabled) {
      return;
    }

    if(this.multiple) {
      let itemIndex = this.findItemIndex(option);
      if(itemIndex != -1)
        this.value.splice(itemIndex, 1);
      else
        this.value.push(option.value);
    }
    else {
      this.value = option.value;
    }

    this.onModelChange(this.value);

    this.onChange.emit({
      originalEvent: event,
      value: this.value
    });
  }

  isSelected(option: SelectItem) {
    if(this.multiple)
      return this.findItemIndex(option) != -1;
    else
      return option.value == this.value;
  }

  findItemIndex(option: SelectItem) {
    let index = -1;
    if(this.value) {
      for(let i = 0; i < this.value.length; i++) {
        if(this.value[i] == option.value) {
          index = i;
          break;
        }
      }
    }
    return index;
  }

  onInputFocus(event){
    if(!this.value){
      this.onItemClick(null,this.options[0]);
    }
    this.focused = true;
  }
  onInputBlur(event){
    this.focused = false;
  }

  onKeydown(event){
    if(event.which <= 36  || event.which >= 41 || this.disabled){
      return;
    }
    let index = 0;
    if(this.multiple && this.value){
      index = this.findItemIndex({value:this.value[this.value.length - 1],label:null,icon:null});
    }else{
      index = this.findItemIndex({value:this.value,label:null,icon:null});
    }
    let cIndex = -1;
    switch (event.which) {
      case 37:
      case 38:
      cIndex = index == -1 ? 0 : index + 1 ;
        break;
      case 39:
      case 40:
        cIndex = index == -1 ? this.options.length - 1 : index - 1;
        break;
    }
    if(cIndex < 0){
      cIndex = this.options.length - 1;
    }
    if(cIndex >= this.options.length){
      cIndex = 0;
    }
    let element = this.options[cIndex];

    this.onItemClick(null,element);
  }

  /**
   * return true if the input has errors
   * @returns {boolean}
   */
  public hasErrors(): boolean {
    return this.message != null && this.message.trim().length > 0;
  }

}

@NgModule({
  imports: [CommonModule],
  exports: [SegmentControlComponent],
  declarations: [SegmentControlComponent]
})
export class PibSegmentControlModule { }



