  getProfessionalCategoryLabel(value){
    let option = null, 
        deeperOption = null;

    option = this.getProfitionalCategories(this._lang).filter((val) => {
      return val.value == value;
    })

    if(option.length == 0){
      option = this.getEmployeeProfessionalCategories(this._lang).filter((val) => {
        if(val.value == 'list'){
          val['content'].map((deepVal) => {
            if(deepVal.value == value){
              deeperOption = deepVal;
            }
          })
        }
        return val.value == value;
      })      
    }

    if(option.length == 0){
      option = this.getIndividualProfessionalCategories(this._lang).filter((val) => {
        return val.value == value;
      })      
    }

    if(option.length == 0){
      option = this.getRetirementProfessionalCategories(this._lang).filter((val) => {
        return val.value == value;
      })      
    }

    if(option.length == 0){
      option = this.getStudentProfessionalCategories(this._lang).filter((val) => {
        return val.value == value;
      })      
    }

    if(!option[0] || !option[0].label){
      
      if(!deeperOption || !deeperOption.label){
        return '-----';  
      }
      else {
        return deeperOption.label
      }
      
    }

    return option[0].label;
  }

