  getPatrimonyAmountsNumber(value){
    let option = this.getPatrimonyAmounts(this._lang).filter((val) => {
      return val.value === value;
    })

    switch(option[0].value){
        case '0':
          return 0;
        case '0-10':
          return 0;
        case '10-50':
          return 10000;
        case '50-100':
          return 50000;
        case '100-200':
          return 100000;
        case '200':
          return 200000;
        default: 
          return 0;
    };
   
  }

