import { Component, HostBinding, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import {HeaderStateService,HEADER_STATE} from '../../services/header-state.service';
import { SharedModelService} from '../../services/shared-model.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition } from '../../routing/router.animations';
import { environment } from '../../../environments/environment'
import {Modal} from '../../components/modal.component';

@Component({
  selector: 'app-follow-account-pref',
  templateUrl: './follow-account-pref.component.html',
  styleUrls: ['./follow-account-pref.component.css'],
  animations: [routerTransition()],
  host: {'[@routerTransition]': ''}
})
export class FollowAccountPrefComponent implements OnInit {

  /**
   * @var {HostBinding} routerAnimation
   * @var {object} environment
   * @var {ViewChild} modalBranchLocator
   * @var {boolean} stepSubmitted
   */
  @HostBinding('@routerTransition') routerAnimation = "next";
  environment = environment;
  @ViewChild('modalBranchLocator') modalBranchLocator: Modal;
  branchLocatorIFrame;
  stepSubmitted: boolean = false;

  /**
   * @constructor
   * Constructor of FollowAccountPrefComponent class
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {RouterStateService} _routerState
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService 
   */
  constructor(private router: Router, private _location: Location, 
              private _headerSteps:HeaderStateService,
              private _sharedModelService: SharedModelService,
              private _fillDataStepService: FillDataStepsChangerService,
              private _routerState: RouterStateService) {
    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
  }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.ACCOUNT,subStep:0});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back()
  }

  openBranchLocator(){
    this.modalBranchLocator._isOpened = true
    this.branchLocatorIFrame = document.getElementById('branchLocatorIFrame');

    this.branchLocatorIFrame.addEventListener("load", function() {
      console.log('=> Iframe loaded');
      let branchLocatorIFrameContent = this.branchLocatorIFrame.contentDocument || this.branchLocatorIFrame.contentWindow.document;
      branchLocatorIFrameContent.querySelector('#searchInput').value = this._sharedModelService.model.informations.contactDetail.zipCode;
      branchLocatorIFrameContent.querySelector('select[name=location_type]').options[0].removeAttribute('selected');
      branchLocatorIFrameContent.querySelector('select[name=location_type]').options[1].setAttribute('selected','selected');
      let textVal = branchLocatorIFrameContent.querySelector('select[name=location_type]').options[1].text;
      branchLocatorIFrameContent.querySelector('#AbslCustomSelectValue .value').textContent = textVal;
      branchLocatorIFrameContent.querySelector('input[type=submit]').click();
    }.bind(this), false);
  }

  openBranchLocatorIfNeeded(){
    if(this._sharedModelService.model.informations.account.agencyChoosed){
      this.next('A');
    }
  }

  closeBranchLocator(){
    this.modalBranchLocator._isOpened = false;
  }

  selectAgency(){
    this.branchLocatorIFrame = document.getElementById('branchLocatorIFrame');
    let branchLocatorIFrameContent = this.branchLocatorIFrame.contentDocument || this.branchLocatorIFrame.contentWindow.document;
    let selectedResult = branchLocatorIFrameContent.querySelector('li.selected h4');
    console.log(selectedResult);
    this.stepSubmitted = true;
    if(selectedResult){
      selectedResult = selectedResult.innerHTML;
      this.stepSubmitted = false;
      this.modalBranchLocator._isOpened = false;
      this._sharedModelService.model.informations.account.agencyType = 'A';
      this._sharedModelService.model.informations.account.agencyChoosed = selectedResult;
      this._sharedModelService.model.informations.account.isAgencyTypeValidated = true;
      this.routerAnimation = 'prev';
      setTimeout(() => {
        this.router.navigate(['your-account']);
      },50)  
    }
    else {
      console.log('ERROR');
      this._sharedModelService.model.informations.account.isAgencyTypeValidated = false;
      this._sharedModelService.model.informations.account.agencyTypeError = 'hsbc-main.error_field_select_agency';
    }
    


  }

  /**
   * Move to the next step
   * @function next
   * @param {string} type Agency type
   */
  next(type) {
    this._sharedModelService.model.informations.account.agencyType = type;

    this._sharedModelService.model.informations.account.isAgencyTypeValidated = true;
    this.routerAnimation = 'prev';
    setTimeout(() => {
      this.router.navigate(['your-account']);
    },50)
  }

}



