import { Component, HostBinding, OnInit } from '@angular/core';
import { TranslateService } from 'ng2-translate';
import { Router } from '@angular/router';
import { Location } from '@angular/common';
import { FillDataStepsChangerService, FILL_DATA_STEPS } from '../../services/fill-data-steps-changer.service';
import { HeaderStateService, HEADER_STATE } from '../../services/header-state.service';
import { SharedModelService } from '../../services/shared-model.service';
import { RouterStateService } from '../../services/router-state.service';
import { routerTransition } from '../../routing/router.animations';
import { CustomPolyfills } from '../../../custom-polyfills'
import {CountriesDataService} from '../../services/countries-data.service';
import {UsefulDataService} from '../../services/useful-data.service';


@Component({
  selector: 'app-recap-validation',
  templateUrl: './recap-validation.component.html',
  styleUrls: ['./recap-validation.component.css'],
  animations: [routerTransition()],
  host: {'[@routerTransition]': ''}
})
export class RecapValidationComponent implements OnInit {

    /**
   * @var {array} numbersAsLetters
   * @var {string} expanded
   */
  numbersAsLetters = ['Seconde', 'Troisième'];
  expanded = null;
  currentDate = new Date();

  @HostBinding('@routerTransition') routerAnimation = "next";

  /**
  *
  * LABELS
  *
  */
  preferredLanguage: string;
  familySituation: string;
  maritalStatus: string;
  birthCountry: string;
  nationality: string;
  otherNationalities= [];
  mainResidenceCountry: string;
  openingHsbcAccountReason: string;
  previousResidenceCountry: string;
  residenceStatus: string;
  postalResidenceCountry: string;
  professionalStatus: string;
  professionalCategory: string;
  totalRevenus: number;
  totalActifs: number;
  typeContrat: string;
  activitySector: string;
  employerCountry: string;
  fiscalCountry: string;
  secondFiscalCountry: string;
  thirdFiscalCountry: string;
  firstCountryPatrimonyOrigin: string;
  secondCountryPatrimonyOrigin: string;
  thirdCountryPatrimonyOrigin: string;
  departmentLabel: string;

  mobilePhoneQS : string;
  homePhoneQS: string;
  
  accountsPatrimony: string;
  financialTitles: string;
  assurancePatrimony: string;
  otherActivePatrimony: string;
  labelsPatrimony: string;

  labelAgency: string;

  emitTransferCountryOne: string;
  emitTransferCountryTwo: string;
  emitTransferCountryThree: string;
  receiveTransferCountryOne: string;
  receiveTransferCountryTwo: string;
  receiveTransferCountryThree: string;

  labelDebit: string;
  labelNoCard: string;
  labelAccountUse: string;
  labelMontantMensuel: string;

  familySituationHolder: string;
  maritalStatusHolder: string;
  birthCountryHolder: string;
  nationalityHolder: string;
  otherNationalitiesHolder= [];
  mainResidenceCountryHolder: string;
  openingHsbcAccountReasonHolder: string;
  previousResidenceCountryHolder: string;
  residenceStatusHolder: string;
  postalResidenceCountryHolder: string;
  professionalStatusHolder: string;
  professionalCategoryHolder: string;
  totalRevenusHolder: number;
  totalActifsHolder: number;
  typeContratHolder: string;
  activitySectorHolder: string;
  employerCountryHolder: string;
  fiscalCountryHolder: string;
  secondFiscalCountryHolder: string;
  thirdFiscalCountryHolder: string;

  mobilePhoneQSHolder : string;
  homePhoneQSHolder: string;

  firstCountryPatrimonyOriginHolder: string;
  secondCountryPatrimonyOriginHolder: string;
  thirdCountryPatrimonyOriginHolder: string;
  
  accountsPatrimonyHolder: string;
  financialTitlesHolder: string;
  assurancePatrimonyHolder: string;
  otherActivePatrimonyHolder: string;
  labelsPatrimonyHolder: string;

  accountsPatrimonyHolderMutual: string;
  financialTitlesHolderMutual: string;
  assurancePatrimonyHolderMutual: string;
  otherActivePatrimonyHolderMutual: string;

  labelAgencyHolder: string;

  emitTransferCountryOneHolder: string;
  emitTransferCountryTwoHolder: string;
  emitTransferCountryThreeHolder: string;
  receiveTransferCountryOneHolder: string;
  receiveTransferCountryTwoHolder: string;
  receiveTransferCountryThreeHolder: string;

  labelDebitHolder: string;
  labelNoCardHolder: string;
  labelAccountUseHolder: string;
  labelMontantMensuelHolder: string;
  departmentLabelHolder: string;

  labelChoixDom: string;

  /**
   * Constructor of RecapValidationComponent class
   * @constructor
   * @param {TranslateService} translate
   * @param {Location} _location location service
   * @param {Router} router router service
   * @param {HeaderStateService} _headerSteps
   * @param {SharedModelService} _sharedModelService Model service
   * @param {FillDataStepsChangerService} _fillDataStepService 
   * @param {RouterStateService} _routerState 
   * @param {CountriesDataService} _countriesService
   */
  constructor(private translate: TranslateService, 
    private router: Router,
    private _location: Location, 
    private _headerSteps: HeaderStateService,
    private _sharedModelService: SharedModelService,
    private _fillDataStepService: FillDataStepsChangerService,
    private _usefuldata: UsefulDataService,
    private _routerState: RouterStateService,
    private _countriesService: CountriesDataService) { 

    this.routerAnimation = this._routerState.lastAction;
    setTimeout(() => {
      this.routerAnimation = 'next'
    }, 500);
  }

  updateForm(){

    this.mobilePhoneQS = this.formatPhoneNumber(this._sharedModelService.model.informations.person.mobilePhoneDialCode +this._sharedModelService.model.informations.person.mobilePhone);
    this.homePhoneQS = this.formatPhoneNumber(this._sharedModelService.model.informations.person.fixePhoneDialCode + this._sharedModelService.model.informations.person.fixePhone);


    this.familySituation = this._usefuldata.getFamilySituationLabel(this._sharedModelService.model.informations.person.familySituation) ;

    if(this._sharedModelService.model.informations.person.maritalStatus){
      this.maritalStatus = this._usefuldata.getMaritalStatusLabels(this._sharedModelService.model.informations.person.maritalStatus) ;
    }

    this._countriesService.getCountryLabel(this._sharedModelService.model.informations.person.birthCountry, (val) => {
      this.birthCountry = val;
    });
    this._countriesService.getCountryLabel(this._sharedModelService.model.informations.person.nationality, (val) => {
      this.nationality = val;
    });

    this.departmentLabel = this._usefuldata.getDepartmentLabel(this._sharedModelService.model.informations.person.zipCode);
    
    this.otherNationalities = [];
    if(this._sharedModelService.model.informations.person.otherNationalities[0]){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.person.otherNationalities[0], (value) => {
        this.otherNationalities[0] = (value);
      });  
    }
    if(this._sharedModelService.model.informations.person.otherNationalities[1]){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.person.otherNationalities[1], (value) => {
        this.otherNationalities[1] = (value);
      });  
    }

    this._countriesService.getCountryLabel(this._sharedModelService.model.informations.contactDetail.country, (val) => {
      this.mainResidenceCountry = val;
    });

    if(this._sharedModelService.model.informations.contactDetail.reasonForCreatingHSBCAccount){
      this.openingHsbcAccountReason = this._usefuldata.getOpeningHsbcAccountReasonLabels(this._sharedModelService.model.informations.contactDetail.reasonForCreatingHSBCAccount) ;
    }

    this.residenceStatus = this._usefuldata.getResidanceStatusLabel(this._sharedModelService.model.informations.contactDetail.mainResidenceStatus);
    
    if(this._sharedModelService.model.informations.contactDetail.hasPreviousAddress){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.contactDetail.previousCountry, (val) => {
        this.previousResidenceCountry = val;
      });  
    }

    if(this._sharedModelService.model.informations.contactDetail.isPostalAddressSameAsResidenceAddress == 'different'){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.contactDetail.postalCountry, (val) => {
        this.postalResidenceCountry = val;
      });  
    }

    if(this._sharedModelService.model.informations.situation.professionalSituation.professionalStatus){
      this.professionalStatus = this._usefuldata.getProfessionalStatusLabel(this._sharedModelService.model.informations.situation.professionalSituation.professionalStatus);  
    }
    
    if(this._sharedModelService.model.informations.situation.professionalSituation.professionalCategory){
      this.professionalCategory = this._usefuldata.getProfessionalCategoryLabel(this._sharedModelService.model.informations.situation.professionalSituation.professionalCategory);   
    }

    if(this._sharedModelService.model.informations.situation.professionalSituation.professionalCategory === 'NA'){
     this.translate.get('hsbc-main.recap_sans_emploi').subscribe((value) => {
        this.professionalCategory = value;
     })
    }
    if(this._sharedModelService.model.informations.situation.professionalSituation.professionalCategory === 'chef_entreprise'){
      this.translate.get('hsbc-main.recap_chef_ent').subscribe((value) => {
        this.professionalCategory = value;
     })
    }
    

    this.typeContrat = this._usefuldata.getTypeContratLabel(this._sharedModelService.model.informations.situation.professionalSituation.contractType); 

    this.activitySector = this._usefuldata.getActivitySectorLabel(this._sharedModelService.model.informations.situation.professionalSituation.activitySector);

    this.totalRevenus = Math.floor(this._sharedModelService.model.informations.situation.monthlyIncome.monthlyNetSalary) + Math.floor(this._sharedModelService.model.informations.situation.monthlyIncome.BICBNCMonthly) + Math.floor(this._sharedModelService.model.informations.situation.monthlyIncome.pensionsMonthlyRetirement) + Math.floor(this._sharedModelService.model.informations.situation.monthlyIncome.allowancesMonthly) + Math.floor(this._sharedModelService.model.informations.situation.monthlyIncome.otherMonthlyIncome);

    if(this._sharedModelService.model.informations.situation.professionalSituation.country){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.situation.professionalSituation.country, (val) => {
        this.employerCountry = val;
      });  
    }

    if(this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidence){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidence, (val) => {
        this.fiscalCountry = val;
      });  
    }

    if(this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidenceTwo){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidenceTwo, (val) => {
        this.secondFiscalCountry = val;
      });  
    }
    if(this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidenceThree){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.situation.taxInformation.countryOfTaxResidenceThree, (val) => {
        this.thirdFiscalCountry = val;
      });  
    }

    if(this._sharedModelService.model.informations.situation.patrimony.originCountryPatrimonyOne){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.situation.patrimony.originCountryPatrimonyOne, (val) => {
        this.firstCountryPatrimonyOrigin = val;
      });  
    }

    if(this._sharedModelService.model.informations.situation.patrimony.originCountryPatrimonyTwo){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.situation.patrimony.originCountryPatrimonyTwo, (val) => {
        this.secondCountryPatrimonyOrigin = val;
      });  
    }

    if(this._sharedModelService.model.informations.situation.patrimony.originCountryPatrimonyThree){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.situation.patrimony.originCountryPatrimonyThree, (val) => {
        this.thirdCountryPatrimonyOrigin = val;
      });  
    }

    

    
    this.totalActifs = 0;

    if(this._sharedModelService.model.informations.situation.patrimony.accountsPatrimony){
      this.accountsPatrimony = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.model.informations.situation.patrimony.accountsPatrimony);
      this.totalActifs += this._usefuldata.getPatrimonyAmountsNumber(this._sharedModelService.model.informations.situation.patrimony.accountsPatrimony);
    }

    if(this._sharedModelService.model.informations.situation.patrimony.financialTitles){
      this.financialTitles = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.model.informations.situation.patrimony.financialTitles);
      this.totalActifs += this._usefuldata.getPatrimonyAmountsNumber(this._sharedModelService.model.informations.situation.patrimony.financialTitles);
    }

    if(this._sharedModelService.model.informations.situation.patrimony.assurancePatrimony){
      this.assurancePatrimony = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.model.informations.situation.patrimony.assurancePatrimony);
      this.totalActifs += this._usefuldata.getPatrimonyAmountsNumber(this._sharedModelService.model.informations.situation.patrimony.assurancePatrimony);
    }

    if(this._sharedModelService.model.informations.situation.patrimony.otherActivePatrimony){
      this.otherActivePatrimony = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.model.informations.situation.patrimony.otherActivePatrimony);
      this.totalActifs += this._usefuldata.getPatrimonyAmountsNumber(this._sharedModelService.model.informations.situation.patrimony.otherActivePatrimony);
    }


    this.labelsPatrimony = '';
    if(this._sharedModelService.model.informations.situation.patrimony.revenu){
       this.translate.get('hsbc-main.patrimony_pro_revenus').subscribe((value) => {
          this.labelsPatrimony += value + ', ';
       })
    }
    if(this._sharedModelService.model.informations.situation.patrimony.heritage){
       this.translate.get('hsbc-main.patrimony_heritage').subscribe((value) => {
          this.labelsPatrimony += value + ', ';
       })
    }
    if(this._sharedModelService.model.informations.situation.patrimony.donation){
       this.translate.get('hsbc-main.patrimony_donation').subscribe((value) => {
          this.labelsPatrimony += value + ', ';
       })
    }
    if(this._sharedModelService.model.informations.situation.patrimony.gain){
       this.translate.get('hsbc-main.patrimony_gains').subscribe((value) => {
          this.labelsPatrimony += value + ', ';
       })
    }

    if(this.labelsPatrimony){
      this.labelsPatrimony = this.labelsPatrimony.slice(0, -2);  
    }

    
    if(this._sharedModelService.model.informations.situation.patrimony.nopatrimoine){
       this.labelsPatrimony = '-----';
    }

    if(this._sharedModelService.model.informations.account.agencyType == 'A'){
      this.labelAgency = this._sharedModelService.model.informations.account.agencyChoosed;
    }
    else {
      this.translate.get('hsbc-main.follow_account_direct_agency').subscribe((value) => {
          this.labelAgency = value;
       }) 
    }


    if(this._sharedModelService.model.informations.account.offer.emitTransactInternationallyCountryOne){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.account.offer.emitTransactInternationallyCountryOne, (val) => {
        this.emitTransferCountryOne = val;
      });  
    }

    if(this._sharedModelService.model.informations.account.offer.emitTransactInternationallyCountryTwo){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.account.offer.emitTransactInternationallyCountryTwo, (val) => {
        this.emitTransferCountryTwo = val;
      });  
    }

    if(this._sharedModelService.model.informations.account.offer.emitTransactInternationallyCountryThree){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.account.offer.emitTransactInternationallyCountryThree, (val) => {
        this.emitTransferCountryThree = val;
      });  
    }

    if(this._sharedModelService.model.informations.account.offer.receiveTransactInternationallyCountryOne){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.account.offer.receiveTransactInternationallyCountryOne, (val) => {
        this.receiveTransferCountryOne = val;
      });  
    }

    if(this._sharedModelService.model.informations.account.offer.receiveTransactInternationallyCountryTwo){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.account.offer.receiveTransactInternationallyCountryTwo, (val) => {
        this.receiveTransferCountryTwo = val;
      });  
    }

    if(this._sharedModelService.model.informations.account.offer.receiveTransactInternationallyCountryThree){
      this._countriesService.getCountryLabel(this._sharedModelService.model.informations.account.offer.receiveTransactInternationallyCountryThree, (val) => {
        this.receiveTransferCountryThree = val;
      });  
    }

    if(this._sharedModelService.model.informations.account.creditCard.debitType == 'CBD'){
      this.translate.get('hsbc-main.card_type_cbd').subscribe((value) => {
          this.labelDebit = value;
       })
    }
    else {
      this.translate.get('hsbc-main.card_type_cbi').subscribe((value) => {
          this.labelDebit = value;
       })
    }

    if(this._sharedModelService.model.informations.account.creditCard.noIntendCard){
       this.translate.get('hsbc-main.card_type_no').subscribe((value) => {
          this.labelNoCard = value;
       })
    }

    if(this._sharedModelService.model.informations.account.accountUsage.domiciliation == 2){
       if(this._sharedModelService.model.informations.account.accountUsage.accountUsageType == 'cp'){
         this.labelAccountUse = 'Compte principal';
       }
       else {
         this.labelAccountUse = 'Compte secondaire';
       }
    }

    if(this._sharedModelService.model.informations.account.accountUsage.amount){
      this.labelMontantMensuel = this._usefuldata.getMonthlyAmountsInTheNext12MonthsLabel(this._sharedModelService.model.informations.account.accountUsage.amount)
    }

  }

  updateFormHolder(){

    this.mobilePhoneQSHolder = this.formatPhoneNumber(this._sharedModelService.modelHolder.informations.person.mobilePhoneDialCode +this._sharedModelService.modelHolder.informations.person.mobilePhone);
    this.homePhoneQSHolder = this.formatPhoneNumber(this._sharedModelService.modelHolder.informations.person.fixePhoneDialCode + this._sharedModelService.modelHolder.informations.person.fixePhone);
    
    if(this._sharedModelService.model.informations.account.accountUsage.accountChoosed && (this._sharedModelService.model.informations.account.accountUsage.accountChoosed == 'M1' || this._sharedModelService.model.informations.account.accountUsage.accountChoosed == 'M2')){
      this.translate.get('hsbc-main.card_utilisation_account_of').subscribe((value) => {
        if(this._sharedModelService.model.informations.account.accountUsage.accountChoosed == 'M1'){
          this.labelChoixDom = value + ' ' + this._sharedModelService.model.informations.person.lastName.toUpperCase() + ' ' + this._sharedModelService.model.informations.person.firstName.toUpperCase();
        }  
        else if(this._sharedModelService.model.informations.account.accountUsage.accountChoosed == 'M2'){
          this.labelChoixDom = value + ' ' + this._sharedModelService.modelHolder.informations.person.lastName.toUpperCase() + ' ' + this._sharedModelService.modelHolder.informations.person.firstName.toUpperCase();
        } 
       })
    }
    else if(this._sharedModelService.model.informations.account.accountUsage.accountChoosed && this._sharedModelService.model.informations.account.accountUsage.accountChoosed == 'M3') {
      this.translate.get('hsbc-main.card_utilisation_joint_account').subscribe((value) => {
         this.labelChoixDom = value;
      })
    }

    this.departmentLabelHolder = this._usefuldata.getDepartmentLabel(this._sharedModelService.modelHolder.informations.person.zipCode);

   
    this.familySituationHolder = this._usefuldata.getFamilySituationLabel(this._sharedModelService.modelHolder.informations.person.familySituation) ;
    
    if(this._sharedModelService.modelHolder.informations.person.maritalStatus){
      this.maritalStatusHolder = this._usefuldata.getMaritalStatusLabels(this._sharedModelService.modelHolder.informations.person.maritalStatus) ;
    }

    this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.person.birthCountry, (val) => {
      this.birthCountryHolder = val;
    });
   
    this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.person.nationality, (val) => {
      this.nationalityHolder = val;
    });
   
    this.otherNationalitiesHolder = [];
    if(this._sharedModelService.modelHolder.informations.person.otherNationalities[0]){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.person.otherNationalities[0], (value) => {
        this.otherNationalitiesHolder[0] = (value);
      });  
    }
   
    if(this._sharedModelService.modelHolder.informations.person.otherNationalities[1]){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.person.otherNationalities[1], (value) => {
        this.otherNationalitiesHolder[1] = (value);
      });  
    }

   
    this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.contactDetail.country, (val) => {
      this.mainResidenceCountryHolder = val;
    });

    if(this._sharedModelService.modelHolder.informations.contactDetail.reasonForCreatingHSBCAccount){
      this.openingHsbcAccountReason = this._usefuldata.getOpeningHsbcAccountReasonLabels(this._sharedModelService.modelHolder.informations.contactDetail.reasonForCreatingHSBCAccount) ;
    }
   
    if(this._sharedModelService.modelHolder.informations.contactDetail.mainResidenceStatus){
      this.residenceStatusHolder = this._usefuldata.getResidanceStatusLabel(this._sharedModelService.modelHolder.informations.contactDetail.mainResidenceStatus);  
    }
    
   
    if(this._sharedModelService.modelHolder.informations.contactDetail.hasPreviousAddress){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.contactDetail.previousCountry, (val) => {
        this.previousResidenceCountryHolder = val;
      });  
    }
   
    
    if(this._sharedModelService.modelHolder.informations.contactDetail.isPostalAddressSameAsResidenceAddress == 'different'){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.contactDetail.postalCountry, (val) => {
        this.postalResidenceCountryHolder = val;
      });  
    }
     

    if(this._sharedModelService.modelHolder.informations.situation.professionalSituation.professionalStatus){
      this.professionalStatusHolder = this._usefuldata.getProfessionalStatusLabel(this._sharedModelService.modelHolder.informations.situation.professionalSituation.professionalStatus);  
    }

    if(this._sharedModelService.modelHolder.informations.situation.professionalSituation.professionalCategory){
      this.professionalCategoryHolder = this._usefuldata.getProfessionalCategoryLabel(this._sharedModelService.modelHolder.informations.situation.professionalSituation.professionalCategory); 
    }

    if(this._sharedModelService.modelHolder.informations.situation.professionalSituation.professionalCategory === 'NA'){
     this.translate.get('hsbc-main.recap_sans_emploi').subscribe((value) => {
        this.professionalCategoryHolder = value;
     })
    }
    if(this._sharedModelService.modelHolder.informations.situation.professionalSituation.professionalCategory === 'chef_entreprise'){
      this.translate.get('hsbc-main.recap_chef_ent').subscribe((value) => {
        this.professionalCategoryHolder = value;
     })
    }

    this.typeContratHolder = this._usefuldata.getTypeContratLabel(this._sharedModelService.modelHolder.informations.situation.professionalSituation.contractType); 
   
    this.totalRevenusHolder = Math.floor(this._sharedModelService.modelHolder.informations.situation.monthlyIncome.monthlyNetSalary) + Math.floor(this._sharedModelService.modelHolder.informations.situation.monthlyIncome.BICBNCMonthly) + Math.floor(this._sharedModelService.modelHolder.informations.situation.monthlyIncome.pensionsMonthlyRetirement) + Math.floor(this._sharedModelService.modelHolder.informations.situation.monthlyIncome.allowancesMonthly) + Math.floor(this._sharedModelService.modelHolder.informations.situation.monthlyIncome.otherMonthlyIncome);

    this.activitySectorHolder = this._usefuldata.getActivitySectorLabel(this._sharedModelService.modelHolder.informations.situation.professionalSituation.activitySector);
    if(this._sharedModelService.modelHolder.informations.situation.professionalSituation.country){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.situation.professionalSituation.country, (val) => {
        this.employerCountryHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.situation.taxInformation.countryOfTaxResidence){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.situation.taxInformation.countryOfTaxResidence, (val) => {
        this.fiscalCountryHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.situation.taxInformation.countryOfTaxResidenceTwo){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.situation.taxInformation.countryOfTaxResidenceTwo, (val) => {
        this.secondFiscalCountryHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.situation.taxInformation.countryOfTaxResidenceThree){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.situation.taxInformation.countryOfTaxResidenceThree, (val) => {
        this.thirdFiscalCountryHolder= val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.situation.patrimony.originCountryPatrimonyOne){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.situation.patrimony.originCountryPatrimonyOne, (val) => {
        this.firstCountryPatrimonyOriginHolder = val;
       
      });  
    }
    if(this._sharedModelService.modelHolder.informations.situation.patrimony.originCountryPatrimonyTwo){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.situation.patrimony.originCountryPatrimonyTwo, (val) => {
        this.secondCountryPatrimonyOriginHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.situation.patrimony.originCountryPatrimonyThree){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.situation.patrimony.originCountryPatrimonyThree, (val) => {
        this.thirdCountryPatrimonyOriginHolder = val;
      });  
    }


    this.totalActifsHolder = 0;

    if(this._sharedModelService.modelHolder.informations.situation.patrimony.accountsPatrimony){
      this.accountsPatrimonyHolder = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.modelHolder.informations.situation.patrimony.accountsPatrimony);
      this.totalActifsHolder += this._usefuldata.getPatrimonyAmountsNumber(this._sharedModelService.modelHolder.informations.situation.patrimony.accountsPatrimony);
    }

    if(this._sharedModelService.modelHolder.informations.situation.patrimony.financialTitles){
      this.financialTitlesHolder = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.modelHolder.informations.situation.patrimony.financialTitles);
      this.totalActifsHolder += this._usefuldata.getPatrimonyAmountsNumber(this._sharedModelService.modelHolder.informations.situation.patrimony.financialTitles);
    }

    if(this._sharedModelService.modelHolder.informations.situation.patrimony.assurancePatrimony){
      this.assurancePatrimonyHolder = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.modelHolder.informations.situation.patrimony.assurancePatrimony);
      this.totalActifsHolder += this._usefuldata.getPatrimonyAmountsNumber(this._sharedModelService.modelHolder.informations.situation.patrimony.assurancePatrimony);
    }

    if(this._sharedModelService.modelHolder.informations.situation.patrimony.otherActivePatrimony){
      this.otherActivePatrimonyHolder = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.modelHolder.informations.situation.patrimony.otherActivePatrimony);
      this.totalActifsHolder += this._usefuldata.getPatrimonyAmountsNumber(this._sharedModelService.modelHolder.informations.situation.patrimony.otherActivePatrimony);
    }

    this.labelsPatrimonyHolder = '';
    if(this._sharedModelService.modelHolder.informations.situation.patrimony.revenu){
       this.translate.get('hsbc-main.patrimony_pro_revenus').subscribe((value) => {
          this.labelsPatrimonyHolder += value + ', ';
       })
    }
    if(this._sharedModelService.modelHolder.informations.situation.patrimony.heritage){
       this.translate.get('hsbc-main.patrimony_heritage').subscribe((value) => {
          this.labelsPatrimonyHolder += value + ', ';
       })
    }
    if(this._sharedModelService.modelHolder.informations.situation.patrimony.donation){
       this.translate.get('hsbc-main.patrimony_donation').subscribe((value) => {
          this.labelsPatrimonyHolder += value + ', ';
       })
    }
    if(this._sharedModelService.modelHolder.informations.situation.patrimony.gain){
       this.translate.get('hsbc-main.patrimony_gains').subscribe((value) => {
          this.labelsPatrimonyHolder += value + ', ';
       })
    }
    if(this.labelsPatrimonyHolder){
      this.labelsPatrimonyHolder = this.labelsPatrimonyHolder.slice(0, -2);  
    }
    
    if(this._sharedModelService.modelHolder.informations.situation.patrimony.nopatrimoine){
       this.labelsPatrimonyHolder = '-----';
    }

    if(this._sharedModelService.modelHolder.informations.account.agencyType == 'A'){
      this.translate.get('hsbc-main.follow_account_remote_agency').subscribe((value) => {
          this.labelAgencyHolder = value;
       })
    }
    else {
      this.translate.get('hsbc-main.follow_account_direct_agency').subscribe((value) => {
          this.labelAgencyHolder = value;
       }) 
    }

    if(this._sharedModelService.model.informations.situation.patrimony.accountsPatrimonyMutual){
      this.accountsPatrimonyHolderMutual = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.model.informations.situation.patrimony.accountsPatrimonyMutual);
    }
    if(this._sharedModelService.model.informations.situation.patrimony.financialTitlesMutual){
      this.financialTitlesHolderMutual = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.model.informations.situation.patrimony.financialTitlesMutual);
    }
    if(this._sharedModelService.model.informations.situation.patrimony.assurancePatrimonyMutual){
      this.assurancePatrimonyHolderMutual = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.model.informations.situation.patrimony.assurancePatrimonyMutual);
    }
    if(this._sharedModelService.model.informations.situation.patrimony.otherActivePatrimonyMutual){
      this.otherActivePatrimonyHolderMutual = this._usefuldata.getPatrimonyAmountsLabels(this._sharedModelService.model.informations.situation.patrimony.otherActivePatrimonyMutual);
    }

    if(this._sharedModelService.modelHolder.informations.account.offer.emitTransactInternationallyCountryOne){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.account.offer.emitTransactInternationallyCountryOne, (val) => {
        this.emitTransferCountryOneHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.account.offer.emitTransactInternationallyCountryTwo){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.account.offer.emitTransactInternationallyCountryTwo, (val) => {
        this.emitTransferCountryTwoHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.account.offer.emitTransactInternationallyCountryThree){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.account.offer.emitTransactInternationallyCountryThree, (val) => {
        this.emitTransferCountryThreeHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.account.offer.receiveTransactInternationallyCountryOne){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.account.offer.receiveTransactInternationallyCountryOne, (val) => {
        this.receiveTransferCountryOneHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.account.offer.receiveTransactInternationallyCountryTwo){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.account.offer.receiveTransactInternationallyCountryTwo, (val) => {
        this.receiveTransferCountryTwoHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.account.offer.receiveTransactInternationallyCountryThree){
      this._countriesService.getCountryLabel(this._sharedModelService.modelHolder.informations.account.offer.receiveTransactInternationallyCountryThree, (val) => {
        this.receiveTransferCountryThreeHolder = val;
      });  
    }
    if(this._sharedModelService.modelHolder.informations.account.creditCard.debitType == 'CBD'){
      this.translate.get('hsbc-main.card_type_cbd').subscribe((value) => {
          this.labelDebitHolder = value;
       })
    }
    else {
      this.translate.get('hsbc-main.card_type_cbi').subscribe((value) => {
          this.labelDebitHolder = value;
       })
    }

    if(this._sharedModelService.modelHolder.informations.account.creditCard.noIntendCard){
       this.translate.get('hsbc-main.card_type_no').subscribe((value) => {
          this.labelNoCardHolder = value;
       })
    }

    if(this._sharedModelService.modelHolder.informations.account.accountUsage.domiciliation == 2){
       if(this._sharedModelService.modelHolder.informations.account.accountUsage.accountUsageType == 'cp'){
         this.labelAccountUseHolder = 'Compte principal';
       }
       else {
         this.labelAccountUseHolder = 'Compte secondaire';
       }
    }

    if(this._sharedModelService.modelHolder.informations.account.accountUsage.amount){
      this.labelMontantMensuelHolder = this._usefuldata.getMonthlyAmountsInTheNext12MonthsLabel(this._sharedModelService.modelHolder.informations.account.accountUsage.amount)
    }
  }

/**
 * Initialisations
 * @function ngOnInit
 */
  ngOnInit() {
    this._fillDataStepService.setCurrentStep({step:FILL_DATA_STEPS.VALIDATION,subStep:0});
    this._headerSteps.setCurrentState(HEADER_STATE.FILL_DATA);
    this.updateForm();
    if(this._sharedModelService.model.accountType == 'jointAccount'){
      this.updateFormHolder();
    }

    this.preferredLanguage = this.translate.getDefaultLang() == 'fr' ? 'Français' : 'Anglais';
    this.translate.onLangChange.subscribe((ev)=>{
      
      if(ev.lang == 'fr'){
        this.preferredLanguage = "Français"
      }
      else {
        this.preferredLanguage = "Anglais" 
      }
      this.updateForm();
      if(this._sharedModelService.model.accountType == 'jointAccount'){
        this.updateFormHolder();
      }
    })
  }

/**
 * Expand drop down list
 * @function expand
 * @param {string} detail Indicates the expanded list among all list
 */
  expand(detail) {
    if (this.expanded == detail) {
      this.expanded = null;
    } else {
      this.expanded = detail;
    }
  }

  /**
   * Move to the previous step
   * @function goBack
   */
  goBack() {
    this._location.back();
  }



  editStep(step){
    if(this._sharedModelService.model.accountType === 'individualAccount'){
      let routeStack = [
                    'your-account', 
                    'personal-information', 
                    'personal-information-second-page', 
                    'coordonees', 
                    'professional-situation', 
                    'monthly-income', 
                    'fiscal-information', 
                    'patrimony', 
                    'follow-account', 
                    'your-account', 
                    'card-type', 
                    'card-utilisation'
                   ];

      for(let i = 0; i < routeStack.length; i++){
        setTimeout(() => {
          this.router.navigate([routeStack[i]]);  
        }, 10)
        if(routeStack[i] == step){
          break;
        }
        
      }  
    }
    else {
      if(step == 'personal-information' || step == 'personal-information-second-page' || step == 'professional-situation' || step == 'monthly-income' || step == 'fiscal-information' || step == 'patrimony' || step == 'card-type'){
        step = step + '/1';
      }
      let routeStack = [
                  'your-account', 
                  'personal-information/1', 
                  'personal-information/2', 
                  'personal-information-second-page/1', 
                  'personal-information-second-page/2', 
                  'coordonees', 
                  'professional-situation/1', 
                  'professional-situation/2', 
                  'monthly-income/1', 
                  'monthly-income/2', 
                  'fiscal-information/1', 
                  'fiscal-information/2', 
                  'patrimony/1', 
                  'patrimony/2', 
                  'follow-account', 
                  'your-account', 
                  'card-type/1', 
                  'card-type/2', 
                  'card-utilisation'
                 ];

       if(this._sharedModelService.model.informations.situation.patrimony.nopatrimoine !== true && this._sharedModelService.modelHolder.informations.situation.patrimony.nopatrimoine !== true){
         routeStack.splice(14, 0, 'patrimony/3');
       }
      for(let i = 0; i < routeStack.length; i++){
        setTimeout(() => {
          this.router.navigate([routeStack[i]]);  
        }, 10)
        if(routeStack[i] == step){
          break;
        }
        
      }
    }
     
  }


  /**
   * Format number for QS
   * @param phone 
   */
  formatPhoneNumber(phone){

    if(phone.substring(0, 4) == "+330"){ phone = (phone.replace("+330","+33"));}  // France
    if(phone.substring(0, 5) == "+2130"){ phone = (phone.replace("+2130","+213"));} // Algerie
    if(phone.substring(0, 4) == "+610"){ phone = (phone.replace("+610","+61"));} // Australia
    if(phone.substring(0, 4) == "+430"){ phone = (phone.replace("+430","+43"));} // Austria
    if(phone.substring(0, 4) == "+320"){ phone = (phone.replace("+320","+32"));} // Belgium
    if(phone.substring(0, 5) == "+5940"){ phone = (phone.replace("+5940","+594"));} // French Guiana
    if(phone.substring(0, 4) == "+490"){ phone = (phone.replace("+490","+49"));} // Germany
    if(phone.substring(0, 5) == "+5900"){ phone = (phone.replace("+5900","+590"));} // Guadeloupe
    if(phone.substring(0, 5) == "+3530"){ phone = (phone.replace("+3530","+353"));} // Ireland
    if(phone.substring(0, 5) == "+5960"){ phone = (phone.replace("+5960","+596"));} // Martinique
    if(phone.substring(0, 5) == "+2120"){ phone = (phone.replace("+2120","+212"));} // Morocco
    if(phone.substring(0, 4) == "+310"){ phone = (phone.replace("+310","+31"));} // Netherlands
    if(phone.substring(0, 5) == "+2620"){ phone = (phone.replace("+2620","+262"));} // Reunion
    if(phone.substring(0, 4) == "+400"){ phone = (phone.replace("+400","+40"));} // Romania
    if(phone.substring(0, 4) == "+410"){ phone = (phone.replace("+410","+41"));} // Suisse
    if(phone.substring(0, 4) == "+440"){ phone = (phone.replace("+440","+44"));} // UK

    return phone;

  }

  /**
   * Move to the next step
   * @function next
   */
  next() {
    this.routerAnimation = 'prev';


    /**
    *
    * SUBMIT EVENT
    * @submit-ng-mono: mono account type
    * @submit-ng-joint: joint account type
    *
    */
    let customEvent = CustomPolyfills.createNewEvent('submit-ng')

    console.log('=> EVENT EMITTED');
   
    window.dispatchEvent( customEvent );

    // setTimeout(() => {
    //   this.router.navigate(['opening-account-information']);
    // },50)
  }

}



