import {
  NgModule, Component, ElementRef, AfterViewInit, OnDestroy, Input, Output, EventEmitter, forwardRef, Renderer
} from '@angular/core';
import {CommonModule} from '@angular/common';
import {DomHandler} from '../common';
import {NG_VALUE_ACCESSOR, ControlValueAccessor} from '@angular/forms';


export const SLIDER_VALUE_ACCESSOR: any = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => SliderComponent),
  multi: true
};

@Component({
  selector: 'pib-slider',
  template: `
    <div *ngIf="showMinMaxLabels && minMaxLabelsPosition == 'top'" class="ui-slider__rangevalues">
      <span class="min__slider"> <span *ngIf="unityPosition == 'left'">{{unity}}</span> {{min}} <span
        *ngIf="unityPosition == 'right'">{{unity}}</span></span>
      <span class="max__slider"> <span *ngIf="unityPosition == 'left'">{{unity}}</span> {{max}} <span
        *ngIf="unityPosition == 'right'">{{unity}}</span></span>
    </div>
    <div [ngStyle]="style"
         [class]="styleClass"
         [ngClass]="{
                 'ui-slider ui-widget ui-widget-content ui-corner-all':true,
                 'ui-state-disabled':disabled,
                 'ui-slider-horizontal' : true,
                 'ui-slider-animate':animate
             }"
         style="margin-top: 40px"
         (click)="onBarClick($event)">
      <div class="ui-helper-hidden-accessible">
        <input #in type="text" readonly (focus)="onInputFocus($event)" (blur)="onInputBlur($event)"
               [attr.aria-labelledby]="labelledby"  [attr.aria-label]="ariaLabel" 
               (keydown)="onKeydown($event)" [disabled]="disabled" [attr.tabindex]="tabindex">
      </div>
      <div
           *ngIf="!range"
           style="
                  position: absolute;
                  z-index: 2;
                  padding: 20px;
                  top: -27px;"
           [style.transition]="dragging ? 'none': null"
           (mousedown)="onMouseDown($event)"
           (touchstart)="onMouseDown($event)"
           [ngStyle]="{'left': 'calc('+handleValue+'% - 30px)'}">
        <span class="ui-slider-handle ui-state-default ui-corner-all" 
              style="display: inline-block; margin-left: 0px; position: static;"
              [ngClass]="{'ui-slider-handle-focus':focus}">
        </span>
      </div>
      <span *ngIf="!range && showToolTip" class="ui-slider-handle__tolip"
            [ngStyle]="{'left': handleValue + '%'}">
                        <span *ngIf="(unity && unityPosition == 'left')">
                              {{unity}}
                        </span>
                        {{value.toFixed(fixed)}}
                        <span *ngIf="unity && (unityPosition == 'right')">
                               {{unity}}
                        </span>
            </span>
      <span *ngIf="!range"
            class="ui-slider-range ui-slider-range-min ui-widget-header ui-corner-all"
            [ngStyle]="{'width': handleValue + '%'}">
                  
            </span>

      <span *ngIf="range"
            (mousedown)="onMouseDown($event,0)"
            (touchstart)="onMouseDown($event,0)"
            [style.transition]="dragging ? 'none': null"
            class="ui-slider-handle ui-state-default ui-corner-all"
            [ngStyle]="{'left':handleValues[0] + '%'}" [ngClass]="{'ui-slider-handle-active':handleIndex==0}"></span>
      <span *ngIf="range && showToolTip"
            class="ui-slider-handle__tolip"
            [ngStyle]="{'left': handleValues[0] + '%'}">
                   <span *ngIf="unity && (unityPosition == 'left')">
                       {{unity}}
                   </span>
                   {{values[0].toFixed(fixed)}}
                   <span *ngIf="unity && (unityPosition == 'right')">
                        {{unity}}
                   </span>
            </span>
      <span *ngIf="range"
            (mousedown)="onMouseDown($event,1)"
            (touchstart)="onMouseDown($event,1)"
            [style.transition]="dragging ? 'none': null"
            class="ui-slider-handle ui-state-default ui-corner-all"
            [ngStyle]="{'left':handleValues[1] + '%'}"
            [ngClass]="{'ui-slider-handle-active':handleIndex==1}"></span>
      <span *ngIf="range && showToolTip" class="ui-slider-handle__tolip"
            [ngStyle]="{'left': handleValues[1] + '%'}">
                        <span *ngIf="unity && (unityPosition == 'left')">
                              {{unity}}
                        </span>
                        {{values[1].toFixed(fixed)}}
                        <span *ngIf="unity && (unityPosition == 'right')">
                               {{unity}}
                        </span>
            </span>

      <span *ngIf="range"
            class="ui-slider-range ui-widget-header ui-corner-all"
            [ngStyle]="{'left':handleValues[0] + '%','width': (handleValues[1] - handleValues[0] + '%')}"></span>
    </div>
    <div *ngIf="showMinMaxLabels && minMaxLabelsPosition == 'bottom'" class="ui-slider__rangevalues">
      <span class="min__slider"> <span *ngIf="unityPosition == 'left'">{{unity}}</span> {{min}} <span
        *ngIf="unityPosition == 'right'">{{unity}}</span></span>
      <span class="max__slider"> <span *ngIf="unityPosition == 'left'">{{unity}}</span> {{max}} <span
        *ngIf="unityPosition == 'right'">{{unity}}</span></span>
    </div>
  `,
  styles: [`
    .ui-slider-handle-focus {
      border-color: #008580;
    }
  `],
  providers: [SLIDER_VALUE_ACCESSOR, DomHandler]
})
export class SliderComponent implements AfterViewInit, OnDestroy, ControlValueAccessor {
  @Input() labelledby: any;
  @Input() ariaLabel: string;

  @Input() animate: boolean;

  @Input() disabled: boolean;

  @Input() min: number = 0;

  @Input() max: number = 100;

  @Input() orientation: string = 'horizontal';

  @Input() step: number;

  @Input() range: boolean;

  @Input() style: any;

  @Input() styleClass: string;

  @Input() showMinMaxLabels = false;

  @Input() minMaxLabelsPosition = 'bottom';

  @Output() onChange: EventEmitter<any> = new EventEmitter();

  @Output() onSlideEnd: EventEmitter<any> = new EventEmitter();

  @Input() unity: string;

  @Input() unityPosition = 'left';

  @Input() showToolTip = true;

  @Input() fixed: number;

  @Input() tabindex: number;

  public focus = false;

  public value: number;

  public values: number[];

  public handleValue: number;

  public handleValues: number[] = [];

  public onModelChange: Function = () => {
  };

  public onModelTouched: Function = () => {
  };

  public dragging: boolean;

  public dragListener: any;

  public touchListener: any;

  public touchupListener: any;

  public mouseupListener: any;

  public initX: number;

  public initY: number;

  public barWidth: number;

  public barHeight: number;

  public sliderHandleClick: boolean;

  public handleIndex: number;

  constructor(public el: ElementRef, public domHandler: DomHandler, public renderer: Renderer) {
  }

  onMouseDown(event: Event, index?: number) {
    if (this.disabled) {
      return;
    }

    this.dragging = true;
    this.updateDomData();
    this.sliderHandleClick = true;
    this.handleIndex = index;
  }

  onBarClick(event) {
    if (this.disabled) {
      return;
    }

    if (!this.sliderHandleClick) {
      this.updateDomData();
      this.handleChange(event);
    }

    this.sliderHandleClick = false;
  }

  ngAfterViewInit() {
    if (this.disabled) {
      return;
    }

    this.dragListener = this.renderer.listenGlobal('body', 'mousemove', (event) => {
      if (this.dragging) {
        this.handleChange(event);
      }
    });
    this.touchListener = this.renderer.listenGlobal('body', 'touchmove', (event) => {
      if (this.dragging) {
        this.handleChange(event);
      }
    });
    this.mouseupListener = this.renderer.listenGlobal('body', 'mouseup', (event) => {
      if (this.dragging) {
        this.dragging = false;
        this.onSlideEnd.emit({originalEvent: event, value: this.value});
      }
    });
    this.touchupListener = this.renderer.listenGlobal('body', 'touchend', (event) => {
      if (this.dragging) {
        this.dragging = false;
        this.onSlideEnd.emit({originalEvent: event, value: this.value});
      }
    });
  }

//TODO add check for type event
  handleChange(event: Event) {
    let handleValue = this.calculateHandleValue(event);
    let newValue = this.getValueFromHandle(handleValue);

    if (this.range) {
      if (this.step) {
        this.handleStepChange(newValue, this.values[this.handleIndex]);
      }
      else {
        this.handleValues[this.handleIndex] = handleValue;
        this.updateValue(newValue, event);
      }
    }
    else {
      if (this.step) {
        this.handleStepChange(newValue, this.value);
      }
      else {
        this.handleValue = handleValue;
        this.updateValue(newValue, event);
      }
    }
  }

  handleStepChange(newValue: number, oldValue: number) {
    console.log('handle step change');
    let diff = (newValue - oldValue);

    if (diff < 0 && (-1 * diff) >= this.step / 2) {
      newValue = oldValue - this.step;
      this.updateValue(newValue);
      this.updateHandleValue();
    }
    else if (diff > 0 && diff >= this.step / 2) {
      newValue = oldValue + this.step;
      this.updateValue(newValue);
      this.updateHandleValue();
    }
  }

  onInputFocus(event) {
    this.focus = true;
  }

  onInputBlur(event) {
    this.focus = false;
  }

  writeValue(value: any): void {
    if (this.range) {
      if (value && (typeof value == 'Array') && value.length > 1) {
        this.values = [value[0], value[1]]
      } else {
        this.values = [this.min, this.max];
      }
    }
    else {
      if (value && (typeof value == "number")) {
        if (value <= this.max && value >= this.min) {
          this.value = value
        }
        else {
          if (value > this.max) {
            this.value = this.max
          } else {
            this.value = this.min
          }
        }
      } else {
        this.value = this.min;
      }

    }


    this.updateHandleValue();
  }

  registerOnChange(fn: Function): void {
    this.onModelChange = fn;
  }

  registerOnTouched(fn: Function): void {
    this.onModelTouched = fn;
  }

  setDisabledState(val: boolean): void {
    this.disabled = val;
  }

  updateDomData(): void {
    let rect = this.el.nativeElement.children[0].getBoundingClientRect();
    this.initX = rect.left + this.domHandler.getWindowScrollLeft();
    this.initY = rect.top + this.domHandler.getWindowScrollTop();
    this.barWidth = this.el.nativeElement.children[0].offsetWidth;
    this.barHeight = this.el.nativeElement.children[0].offsetHeight;
  }

  getPageX(e) {
    if (e instanceof MouseEvent) {
      return e.pageX;
    }

    // Native touch event.
    if (typeof TouchEvent !== 'undefined' && e instanceof TouchEvent) {
      return e.touches.item(0).pageX;
    }

    // jQuery mouse event
    if (e.originalEvent instanceof MouseEvent) {
      return e.originalEvent.pageX;
    }

    // jQuery touch event.
    if (typeof TouchEvent !== 'undefined' &&
      e.originalEvent instanceof TouchEvent) {
      return e.originalEvent.touches[0].pageX;
    }
  }

  getPageY(e) {
    if (e instanceof MouseEvent) {
      return e.pageY;
    }

    // Native touch event.
    if (typeof TouchEvent !== 'undefined' && e instanceof TouchEvent) {
      return e.touches.item(0).pageY;
    }

    // jQuery mouse event
    if (e.originalEvent instanceof MouseEvent) {
      return e.originalEvent.pageY;
    }

    // jQuery touch event.
    if (typeof TouchEvent !== 'undefined' &&
      e.originalEvent instanceof TouchEvent) {
      return e.originalEvent.touches[0].pageY;
    }
  }

  onKeydown(event) {
    let step = (this.max - this.min) / 100;
    let handleValue = 0;
    let newValue = 0;
    switch (event.which) {
      //up
      case 37:
        handleValue = this.handleValue - 1;
        newValue = this.getValueFromHandle(handleValue);
        this.handleValue = handleValue;
        this.updateValue(newValue, event);
        break;
      case 39:
        handleValue = this.handleValue + 1;
        newValue = this.getValueFromHandle(handleValue);
        this.handleValue = handleValue;
        this.updateValue(newValue, event);
        break;
    }
  }

  calculateHandleValue(event): number {
    if (this.orientation === 'horizontal') {
      return Math.floor(((this.getPageX(event) - this.initX) * 100) / (this.barWidth));
    }

    else
      return Math.floor((((this.initY + this.barHeight) - this.getPageY(event)) * 100) / (this.barHeight));
  }

  updateHandleValue(): void {
    if (this.range) {
      this.handleValues[0] = (this.values[0] < this.min ? 0 : this.values[0] - this.min) * 100 / (this.max - this.min);
      this.handleValues[1] = (this.values[1] > this.max ? 100 : this.values[1] - this.min) * 100 / (this.max - this.min);
    }
    else {
      if (this.value < this.min)
        this.handleValue = 0;
      else if (this.value > this.max)
        this.handleValue = 100;
      else
        this.handleValue = (this.value - this.min) * 100 / (this.max - this.min);
    }
  }

  updateValue(val: number, event?: Event): void {
    if (this.range) {
      let value = val;

      if (this.handleIndex == 0) {
        if (value < this.min) {
          value = this.min;
          this.handleValues[0] = 0;
        }
        else if (value > this.values[1]) {
          value = this.values[1];
          this.handleValues[0] = this.handleValues[1];
        }
      }
      else {
        if (value > this.max) {
          value = this.max;
          this.handleValues[1] = 100;
        }
        else if (value < this.values[0]) {
          value = this.values[0];
          this.handleValues[1] = this.handleValues[0];
        }
      }

      this.values[this.handleIndex] = value;
      this.onModelChange(this.values);
      this.onChange.emit({event: event, values: this.values});
    }
    else {
      if (val < this.min) {
        val = this.min;
        this.handleValue = 0;
      }
      else if (val > this.max) {
        val = this.max;
        this.handleValue = 100;
      }

      this.value = Math.floor(val);
      this.onModelChange(this.value);
      this.onChange.emit({event: event, value: this.value});
    }
  }

  getValueFromHandle(handleValue: number): number {
    return (this.max - this.min) * (handleValue / 100) + this.min;
  }

  ngOnDestroy() {
    if (this.dragListener) {
      this.dragListener();
    }

    if (this.mouseupListener) {
      this.mouseupListener();
    }
    if (this.touchListener) {
      this.touchListener()
    }
    if (this.touchupListener) {
      this.touchupListener()
    }
  }
}

@NgModule({
  imports: [CommonModule],
  exports: [SliderComponent],
  declarations: [SliderComponent]
})
export class PibSliderModule {
}


